    background-image: url("x.0b0cda49.svg");
  }

  .search-btn {
    position: absolute;
    bottom: 8px;
    left: 15px;
  }

  .outline {
    width: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
  }

  .menu {
    width: 100vw;
    height: 100vh;
    padding-top: 68px;
    list-style: none;
    display: block;
  }

  .menu-item {
    position: relative;
  }

  .menu-item a {
    color: #111321;
    padding: 11px 0 11px 62px;
    font-family: Manrope;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px;
    text-decoration: none;
    display: block;
  }

  .menu-item a:before {
    content: "";
    width: 32px;
    height: 32px;
    color: #f4f4f4;
    background-color: #4440f6;
    background-position: center;
    background-repeat: no-repeat;
    border: 1px solid #4440f6;
    border-radius: 50px;
    display: block;
    position: absolute;
    top: 50%;
    left: 16px;
    transform: translateY(-50%);
  }

  .menu-item-home a:before {
    background-image: url("home-icon.3efb8e3a.svg");
  }

  .menu-item-favor a:before {
    background-image: url("favorites-icon.d374ce91.svg");
  }

  .menu-item-read a:before {
    background-image: url("book-open.d08ae704.svg");
  }

  .menu-item-active {
    background-color: #4440f6;
  }

  .menu-item-active a {
    color: #f4f4f4;
  }

  .menu-item-active a:before {
    border: 1px solid #f4f4f4;
  }

  .menu-item-active a:after {
    content: "";
    width: 24px;
    height: 24px;
    background-image: url("next-icon.a2ec054f.svg");
    background-position: center;
    background-repeat: no-repeat;
    display: block;
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translateY(-50%);
  }

  .switch {
    width: 40px;
    height: 20px;
    display: inline-block;
    position: relative;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    cursor: pointer;
    background-color: #fff;
    transition: all .4s;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .slider:before {
    content: "";
    width: 16px;
    height: 16px;
    background-color: #4b48db;
    transition: all .4s;
    position: absolute;
    bottom: 2px;
    left: 1px;
  }

  input:checked + .slider {
    background-color: #4b48db;
  }

  input:checked + .slider:before {
    background-color: #fff;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #4b48db;
  }

  input:checked + .slider:before {
    transform: translateX(22px);
  }

  .slider.round {
    border-radius: 20px;
  }

  .slider.round:before {
    border-radius: 50%;
  }

  .theme-switcher {
    margin-bottom: 18px;
    display: flex;
  }

  .light-theme-icon {
